.search {
  width: 100%;
  position: end;
  display: flex;
}

.searchTerm {
  width: 50%;
  border: 3px solid rgb(0, 9, 10);
  border-right: none;
  padding: 5px;
  height: 20px;
  border-radius: 5px 0 0 5px;
  outline: none;
  color: rgb(244, 247, 246);
}

.searchTerm:focus{
  color: rgb(1, 11, 19);
}

.searchButton {
  width: 40px;
  height: 36px;
  border: 1px solid rgb(245, 249, 250);
  background: rgb(241, 244, 245);
  text-align: cright;
  color: rgb(68, 66, 66);
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

/*Resize the wrap to see the search bar change!*/
.wrap{
  width: 30%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}



